    display: table-cell !important;
  }
}

.up-visible-print-block {
  display: none !important;
}

@media print {
  .up-visible-print-block {
    display: block !important;
  }
}

.up-visible-print-inline {
  display: none !important;
}

@media print {
  .up-visible-print-inline {
    display: inline !important;
  }
}

.up-visible-print-inline-block {
  display: none !important;
}

@media print {
  .up-visible-print-inline-block {
    display: inline-block !important;
  }
}

@media print {
  .up-hidden-print {
    display: none !important;
  }
}
